// gradients
.calor {
  background: linear-gradient(45deg, red, yellow);
  &:before {
    background: linear-gradient(45deg, red, yellow);
  }
}

.agua {
  background: linear-gradient(45deg, blue, cyan);
  &:before {
    background: linear-gradient(45deg, blue, cyan);
  }
}

.mora {
  background: linear-gradient(45deg, indigo, magenta);
  &:before {
    background: linear-gradient(45deg, indigo, magenta);
  }
}

.muerdes {
  background: linear-gradient(45deg, green, yellowgreen);
  &:before {
    background: linear-gradient(45deg, green, yellowgreen);
  }
}

.rosa {
  background: linear-gradient(45deg, red, pink);
  &:before {
    background: linear-gradient(45deg, red, pink);
  }
}

.js {
  background: linear-gradient(45deg, orange, yellow);
  &:before {
    background: linear-gradient(45deg, orange, yellow);
  }
}

.ts {
  background: linear-gradient(45deg, blue, teal);
  &:before {
    background: linear-gradient(45deg, blue, teal);
  }
}

.gq {
  background: linear-gradient(45deg, crimson, magenta);
  &:before {
    background: linear-gradient(45deg, crimson, magenta);
  }
}

.mg {
  background: linear-gradient(45deg, yellowgreen, cyan);
  &:before {
    background: linear-gradient(45deg, yellowgreen, cyan);
  }
}

.space {
  background: linear-gradient(45deg, red, indigo);
  &:before {
    background: linear-gradient(45deg, red, indigo);
  }
}

.next {
  background: linear-gradient(45deg, purple, #333);
  &:before {
    background: linear-gradient(45deg, purple, #333);
  }
}

.pj {
  background: linear-gradient(45deg, blue, teal);
  &:before {
    background: linear-gradient(45deg, blue, teal);
  }
}
