.hero {
  width: 100%;
  height: 100vh;
  overflow: hidden;
  background: #222;
  position: relative;

  &:before {
    content: "";
    font-weight: 300;
    height: 100vh;
    width: 1rem;
    z-index: 0;
    background: linear-gradient(to top, red, yellow);
    filter: blur(10px);
    position: absolute;
    animation: anim 3s linear infinite;
  }
}

.balls {
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 0;
  top: 0;
  left: 0;
}

.main-title {
  color: white;
  padding: 0 2rem;
  font-size: 3rem;
  margin-top: 4rem;
  margin-left: 2rem;
  z-index: 10;

  h1 {
    font-weight: 300;
    z-index: 3;
    position: relative;
    //animation: beat 100ms linear infinite;
  }
  h2 {
    font-weight: 100;

    &:after {
      content: "|";
      font-weight: 300;
      color: yellow;
      animation: anim 3s linear infinite;
    }
  }
}

@media screen and (min-width: 600px) {
  .container {
    width: 100%;
  }
}

.ball {
  position: absolute;
  top: 0;
  left: 0;
  width: 100px;
  height: 100px;
  display: inline-block;
  z-index: -1;
  border-radius: 50%;

  &:hover {
    cursor: pointer;
    filter: blur(15px);
  }
}
