// tecnologies
.tecnologies {
  background-color: transparent;
  padding: 2rem 4rem;
  margin: 0 auto;
  display: flex;
  z-index: 1;
  flex-wrap: wrap;
  justify-content: center;

  .card {
    height: 200px;
    width: 300px;
    margin: 0.5rem;
    border-radius: 10px;
    padding: 2rem;
    color: white;
    position: relative;
    z-index: 1;
    transition: transform 0.5s;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    img {
      height: 100%;
    }
    &:hover {
      transform: scale(1.1);
    }
  }

  .t-title {
    width: 100%;
    text-align: center;
    color: white;
    font-family: "Lato", sans-serif;
    font-size: 2rem;
    font-weight: 300;
  }

  .glowing {
    position: relative;

    &::before {
      content: "";
      top: 0;
      left: 0;
      position: absolute;
      height: 100%;
      width: 100%;
      z-index: -1;
      border-radius: 10px;
      filter: blur(5px);
    }
  }
}

@media screen and (min-width: 600px) {
  .card {
    width: 250px;
  }
}
