@import url("https://fonts.googleapis.com/css2?family=Lato:wght@100;300;400;700&display=swap");
@import "./home/hero";
@import "./home/tecnologies";
@import "./home/projects";
@import "./animations";
@import "./gradients";

$back-color: #222;

*,
*:before,
*:after {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  background: $back-color;
  min-height: 100vh;
  width: 100vw;
  font-family: "Lato", sans-serif;
  overflow-x: hidden !important;
}

.container {
  width: 100%;
  margin: 0 auto;
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
}

.content {
}

.particles {
  position: fixed;
  top: 0;
  z-index: -2;
  animation: anim 3s linear infinite;
}

// scrollbar
/* width */
::-webkit-scrollbar {
  width: 15px;
}

/* Track */
::-webkit-scrollbar-track {
  border-radius: 10px;
  background: #333;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: linear-gradient(90deg, indigo, magenta);
  border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(90deg, green, yellowgreen);
}
