// hue-color
@keyframes anim {
  from {
    filter: hue-rotate(0deg);
  }
  to {
    filter: hue-rotate(360deg);
  }
}

@keyframes beat {
  0% {
    transform: scale(1);
    filter: blur(0px) hue-rotate(0) translatex(-1px);
  }
  100% {
    transform: scale(1.1) translatex(1px);
    filter: blur(1px) hue-rotate(180deg);
  }
}
